<template>
    <div class="integral-list">
        <div class="integral-content" v-infinite-scroll="requestData" infinite-scroll-disabled="cellLoading" infinite-scroll-distance="20" infinite-scroll-immediate-check="false">
            <li v-for="(dataItem, index) in goodsArr" :key="dataItem.id"> 
              <integral-cell class="integral-cell" :cellindex="index" :goods="dataItem" @click.native="cellClickHandle" >
              </integral-cell>
            </li>
        </div>
        <empty-bg :msg="emptyMsg" v-show="showEmpty"></empty-bg>
    </div>
</template>

<script>
import Vue from "vue";
import { mapState } from "vuex";
import { formatDate } from "../../../common/js/date.js";
import EmptyBg from "../../popupBox/emptyBg.vue";
import integralCell from "../../public/integralCell.vue";
import { InfiniteScroll } from "mint-ui";
Vue.use(InfiniteScroll);

export default {
  name: "integral-list",
  data() {
    return {
      goodsArr: [],
      showEmpty: false,
      emptyMsg: "您还未购买过积分商品!",
      memoryScroll: 0,
      cellLoading: true,
      curPage: 0,
      pageNum: 10,
      existIdArr: []
    };
  },
  components: {
    EmptyBg,
    integralCell
  },
  activated() {
    document.body.scrollTop = this.memoryScroll;
  },
  beforeRouteLeave(to, from, next) {
    this.memoryScroll = document.body.scrollTop;
    next();
  },
  created() {
    this.requestData(false);
  },
  methods: {
    cellClickHandle(event) {
      let curTarget = event.currentTarget;
      let cellIndex = curTarget.getAttribute("cellindex");
      let dataItem = this.goodsArr[cellIndex];
      if (dataItem.hadUesed) return;
      this.$router.push({
        name: "integralQRcode",
        params: {
          id: dataItem.id,
          name: dataItem.names,
          imgUrl: dataItem.url,
          num: dataItem.num
        }
      });
    },
    requestData(infinite = true) {
      if (this.$route.name != "integralList") return;
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + `php/Route.php`, {
          type: "sourcebuyhistroy",
          uid: this.userid,
          page: this.curPage
        })
        .then(response => {
          this.$store.dispatch("toggleLoading", false);
          if (infinite && this.curPage == 0) return;
          if (infinite && this.cellLoading) return;
          this.cellLoading = true;
          let serverData = response.body.arr;
          if (!serverData || serverData.length == 0) {
            //数据为空
            this.cellLoading = true;
            if (this.curPage == 0) {
              this.showEmpty = true;
            }
          } else {
            //有数据
            if (serverData.length % this.pageNum == 0) {
              //满一页数据
              this.curPage++;
              this.cellLoading = false;
            } else {
              //不满一页
              this.cellLoading = true;
            }
            let detailsArr = this.analysisData(serverData);
            for (let i = 0; i < detailsArr.length; i++) {
              let id = detailsArr[i].id;
              if (this.existIdArr.indexOf(id) >= 0) continue;
              this.goodsArr.push(detailsArr[i]);
              this.existIdArr.push(id);
            }
          }
        })
        .catch(response => {
          console.log(response);
          this.$store.dispatch("toggleLoading", false);
        });
    },
    analysisData(data) {
      let detailsArr = [];
      data.forEach((val, index, arr) => {
        let tempDate = new Date(val.times * 1000);
        let buyTime = formatDate(tempDate, "yyyy-MM-dd");
        let usetime = "";
        let ticketUsed;
        if (val.state.toString() == "2") {
          ticketUsed = true;
          tempDate = new Date(val.usetime * 1000);
          usetime = formatDate(tempDate, "yyyy-MM-dd");
        } else {
          ticketUsed = false;
        }
        detailsArr.push({
          id: val.oid.toString(),
          names: val.gname,
          url: this.path + "img/goods2.jpg",
          buytm: buyTime,
          price: val.source,
          dec: val.message,
          hadUesed: ticketUsed,
          num: val.nums,
          usetm: usetime
        });
      });
      return detailsArr;
    }
  },
  computed: {
    ...mapState(["path", "userid"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.integral-list {
  // height: 100%;
  .integral-content {
    width: 100%;
    padding-bottom: 1px;

    .integral-cell {
      width: 100%;
      background-color: #fff;
      margin-bottom: 0.07rem;
    }
  }

  .empty-bg {
    height: 75vh;
  }
}
</style>



