<template>
    <div>
        <div class="integral-cell-up">
            <span class="iconfont icon-jifen1"></span>
            <span class="integral-name">{{goods.names}}</span>
            <span class="iconfont icon-toright"></span>
        </div>
        <div class="integral-cell-middle">
            <div class="right-float">
                <span class="price">{{goods.price}}积分</span>
            </div>
            <div v-show="goods.num!=1" class="num-div">
                <span class="num">{{goods.num}}</span>
            </div>
            <div class="cell-info">
                <div class="cell-info-left">
                    <div class="img-container">
                        <img v-lazy="goods.url" class="img" />
                    </div>
                </div>
                <div class="cell-info-right">
                    <p class="dec">{{goods.dec}}</p>
                    <p class="buy-time">购买时间：{{goods.buytm}}</p>
                    <span class="medal">正品</span>
                    <span class="medal">积分享兑</span>
                </div>
            </div>
        </div>
        <div class="integral-cell-down">
            <div v-show="!goods.hadUesed" class="middle-item click-item">查看二维码</div>
            <div v-show="goods.hadUesed" class="middle-item had-used">使用于：{{goods.usetm}}</div>
        </div>
    </div>
</template>

<script>
import Vue from 'vue';
import { mapState } from "vuex";
import { Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
Vue.component(Button.name, Button);

export default {
  name: "integral-cell",
  data() {
    return {};
  },
  props: {
    goods: {
      type: Object,
      default: function() {
        return {
          url: "http://www.jubenxiong.com/aml/img/nx_1515661463680.png",
          names: "380美容套餐",
          dec:"",
          buytm: "",
          price: 333,
          hadUesed: true,
          num:10,
          usetm:""
        };
      }
    }
  },
  components: {},
  activated() {},
  created() {},
  computed: {
    ...mapState(["openid", "path"])
  },
  methods: {}
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.integral-cell-up{
    width: 100%;
    height 30px;
    line-height 30px;
    color #42495C;
    .icon-jifen1{
        padding-left 10px;
        font-size 20px;
    }
    .integral-name{
        font-size 15px;
        word-wrap: break-word;
        font-weight: 700;
    }
    .icon-toright{
        font-size 13px;
        color #acacac;
        font-weight: 700;
    }
}
.integral-cell-middle {
    margin 0px 12px 0px 0px;
    position: relative;
    background-color: #efefef;
    border-radius 0px 15px 0px 0px;

    .cell-info {
        font-size: 0px;
        display: flex;
        padding: 7px 0px 11px 0px;

        .cell-info-left {
            flex: 0 0 80px;
            text-align: center;
            
            .img-container {
                width: 60px;
                height: 60px;
                display: inline-block;

                .img {
                    position: relative;
                    width: 100%;
                    height: 100%;
                    // border-radius: 4px;
                }
            }
        }

        .cell-info-right {
            flex: 1;

            .dec {
                margin-top 2px;
                font-size: 13px;
                margin-bottom: 3px;
                color: #42495C;
            }

            .buy-time {
                font-size: 12px;
                line-height: 14px;
                color: #acacac;
                margin-bottom: 2px;
            }

            .medal{
                display inline-block;
                color #FD8709;
                font-size 12px;
                padding: 1px 4px;
                border-radius 4px;
                border 1px #FD8709 solid;
                margin-right 2px;
            }
        }
    }

    .right-float {
        position: absolute;
        right: 12px;
        top: 50%;
        transform: translate(0, -50%);
        text-align: center;

        .price {
            color: #FD8709;
            font-size: 14px;
        }
    }

    .num-div{
        position: absolute;
        right: 0px;
        bottom: -5px;
        transform: translate(0, -50%);
        text-align: center;
        .num{
            display inline-block;
            line-height 14px;
            font-size: 14px;
            color: #fff;
            padding: 0px 3px 0px 6px;
            background-color #7B808D;
            border-radius 50% 0 0px 50%;
        }
    }
}
.integral-cell-down{
    width 100%;
    height 40px;
    text-align right;
    position relative;

    .middle-item{
        display inline-block;
        right: 12px;
        top: 50%;
        transform: translate(0, -50%);
        position: absolute;
    }

    .click-item{
        color #222;
        font-size 12px;
        line-height 12px;
        padding: 6px; 
        border-radius 15px;
        border 1px #acacac solid;
    }
    .had-used{
        color #999;
        font-size 12px;
    }
}
</style>



